.button {
  position: relative;
  height: 60px;
  min-width: 200px;
  background: none;
  border: none;
  color: white;
  border-radius: 60px;
  padding: 0 25px 0 40px;
  /* margin-left: 10px; */
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-image: -webkit-gradient(linear, left top, right top, from(var(--orange)), to(rgba(0, 0, 0, 0)));
  background-image: linear-gradient(to right, var(--orange) 0%, rgba(0, 0, 0, 0) 100%);
  background-color: var(--purple);
  font-size: 18px;
  cursor: pointer;
  -webkit-transition: 600ms all;
  transition: 600ms all
}

.button:hover {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
  background-color: var(--dark-purple);
}

.logo {
  top: 8px;
  left: -10px;
  width: 42px;
  height: 42px;
  position: absolute;
  z-index: 1;
}

.text {
  font-size: 16px;
  font-weight: 600;
}

.withMetamask {
  margin-left: 6px;
  opacity: .7;
  font-size: 12px;
  font-weight: 300;
}

.amounts {
  font-weight: 400;
}

.amountSmall {
  font-size: 14px;
}

.conversion {
  opacity: .7;
  font-weight: 300;
}

.approx {
  margin: 0 5px;
}

.status {
  font-size: 14px;
}

img.symbolEthLogo {
  width: 15px;
}

.symbolEthLogo {
  position: relative;
  top: -1px;
  margin-left: 2px;
  display: none;
}
.symbolEth {
  margin-left: 5px;
}


